import React, { Component, useEffect } from 'react';
import { withRouter } from 'react-router';
import { connect } from 'react-redux';
import Blogs from '@components/Blogs';
import SingleCourse from '@components/SingleCourse';
import DocumentSettings from '@utils/seo';
import { getPopularArticles } from '@modules/cms';

function BlogsScreen(props: any) {
    const seo = {
        title: "Yazılarım | Can Gökçeaslan",
        description: "En son yazılarıma ulaşmak için hemen ziyaret edebilirsin",
        image: props?.cms?.popularArticles?.[0]?.coverImage?.data?.[0]?.attributes?.url || "https://mapandan.gov.ph/wp-content/uploads/2018/03/no_image.jpg",
        keywords: "cangokceaslan, Blog, Son Yazılarım, Yazılım, Node.js, Http, Express, Can Gökçeaslan, Can Gokceaslan, CanGokceaslan, Node.js, NodeJs, JavaScript, Fullstack Developer, CoFounder, Girişim",
        author: "Can Gökçeaslan",
        locale: "tr",
        siteName: "https://www.cangokceaslan.com/yazilarim"
    }

    useEffect(() => {
        props.getPopularArticles().then((res: any) => { });
    }, [])

    return (
        <React.Fragment>
            <DocumentSettings docData={seo} />
            <Blogs hideTitle={true} data={props?.cms?.popularArticles} />
            <SingleCourse />
        </React.Fragment>
    )
}

export default connect((state: any) => ({
    cms: state.cms
}), {
    getPopularArticles: getPopularArticles
})(withRouter(BlogsScreen));